.webgl {
  outline: none;
  position: absolute;
	cursor: pointer;
}

.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  background-color: white;
	align-items: center;
	justify-content: flex-start;
	position: relative;
}

.content {
  z-index: 10;
  display: flex;
  flex-direction: column;
  font-family: "Poppins";
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 3vw;
	background: linear-gradient(
    180deg,
    rgba(0, 0, 30, 0.18) 20.59%,
    rgba(255, 255, 255, 0.00) 70.63%
  );
  backdrop-filter: blur(8px);
	left: 0;
	position: absolute;
}

h1,
h3 {
  color: white;
  margin: 0;
}

h1 {
	font-size: 6vw;
}

h3 {
	font-size: 1.2vw;
  font-weight: 400;
  margin: 8px 8px 8px 0;
}

.link {
  text-decoration: underline;
  color: white;
}

.icons {
  color: white;
	display: flex;
	padding: 8px 8px 8px 0px;
	gap: 16px;
	font-size: 24px;
}

i {
  cursor: pointer;
}

.click {
	position: absolute;
	color: white;
	bottom: 12px;
	right: 12px;
	font-family: "Poppins";
}

