@media only screen and (min-width: 2000px) {
	h1 {
		font-size: 6vw !important;
	}
	h3 {
		font-size: 1.2vw !important;
	}
}

@media only screen and (max-width: 500px) {
	.webgl {
		display: none;
	}

	.content {
		width: 100vw;
		height: 100vh;
		background-color: black;
		padding: 16px;
	}

	h1 {
		font-size: 36px;
	}

	h3 {
		font-size: 11px;
	}

	.icons {
		font-size: 20px;
	}
}